import React from 'react'
import { useTodos } from '../../store'
import { Link } from 'react-router-dom'

const tomorrow = () => {
  const cifri = useTodos(state => state.massiv)
  console.log(cifri);


  return (
    <div>
      <button>
        <Link to='/'>Погода сейчас</Link>
      </button>
      <div>tomorrow</div>
      <div>{cifri}</div>
    </div>
    
  )
}

export default tomorrow